import "./style.css";
import * as THREE from "three";
import * as dat from "lil-gui";
import gsap from "gsap";
import { FontLoader } from "three/examples/jsm/loaders/FontLoader.js";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";

/**
 * Debug
 */
/* const gui = new dat.GUI(); */

const parameters = {
  materialColor1: "#ff0000",
  materialColor2: "#00ff00",
  materialColor3: "#4488ff",
  materialColor4: "#ffff00",
};

/**
 * Base
 */
// Canvas
const canvas = document.querySelector("canvas.webgl");

// Scene
const scene = new THREE.Scene();

/* const axesHelp = new THREE.AxesHelper();
scene.add(axesHelp); */

/**
 * Light
 */
const directionalLight = new THREE.DirectionalLight("#fff", 1);
directionalLight.position.set(1, 1, 0);
scene.add(directionalLight);

/**
 * Objects
 */
//Texture
const textureLoader = new THREE.TextureLoader();
const gradientTexture = textureLoader.load("./textures/gradients/5.jpg");
gradientTexture.magFilter = THREE.NearestFilter;

//Material
const material1 = new THREE.MeshToonMaterial({
  color: parameters.materialColor1,
  gradientMap: gradientTexture,
});
const material2 = new THREE.MeshToonMaterial({
  color: parameters.materialColor2,
  gradientMap: gradientTexture,
});

const material3 = new THREE.MeshToonMaterial({
  color: parameters.materialColor3,
  gradientMap: gradientTexture,
});

const material4 = new THREE.MeshToonMaterial({
  color: parameters.materialColor4,
  gradientMap: gradientTexture,
});
// Meshes
const objectsDistance = 4;

const mesh1 = new THREE.Mesh(new THREE.SphereGeometry(0.3, 32, 32), material1);
const mesh2 = new THREE.Mesh(new THREE.SphereGeometry(0.1, 32, 32), material2);
const mesh3 = new THREE.Mesh(new THREE.SphereGeometry(0.25, 32, 32), material3);
const mesh4 = new THREE.Mesh(new THREE.SphereGeometry(0.33, 32, 32), material4);

mesh1.position.y = -objectsDistance * 0 - 0.5;
mesh2.position.y = -objectsDistance * 1 - 0.2;
mesh3.position.y = -objectsDistance * 2 - 0.9;
mesh4.position.y = -objectsDistance * 3 - 1.4;

mesh1.position.x = 0.6;
mesh2.position.x = -0.8;
mesh3.position.x = -0.4;
mesh4.position.x = -0.7;

scene.add(mesh1, mesh2, mesh3, mesh4);

const sectionMeshes = [mesh1, mesh2, mesh3, mesh4];

/**
 * Particles
 */

//Geometry
const particlesCount = 200;
const positions = new Float32Array(particlesCount * 3);

for (let i = 0; i < particlesCount; i++) {
  positions[i * 3 + 0] = (Math.random() - 0.5) * 10;
  positions[i * 3 + 1] =
    objectsDistance * 0.5 -
    Math.random() * objectsDistance * sectionMeshes.length;
  positions[i * 3 + 2] = (Math.random() - 0.5) * 10;
}

const particlesGeometry = new THREE.BufferGeometry();
particlesGeometry.setAttribute(
  "position",
  new THREE.BufferAttribute(positions, 3)
);

//Material
const particlesMaterial = new THREE.PointsMaterial({
  color: parameters.materialColor,
  sizeAttenuation: true,
  size: 0.03,
});

//Points
const particles = new THREE.Points(particlesGeometry, particlesMaterial);
scene.add(particles);
/**
 * Sizes
 */
const sizes = {
  width: window.innerWidth,
  height: window.innerHeight,
};

window.addEventListener("resize", () => {
  // Update sizes
  sizes.width = window.innerWidth;
  sizes.height = window.innerHeight;

  // Update camera
  camera.aspect = sizes.width / sizes.height;
  camera.updateProjectionMatrix();

  // Update renderer
  renderer.setSize(sizes.width, sizes.height);
  renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
});

/**
 * Camera
 */
//Group
const cameraGroup = new THREE.Group();
scene.add(cameraGroup);

// Base camera
const camera = new THREE.PerspectiveCamera(
  35,
  sizes.width / sizes.height,
  0.1,
  100
);
camera.position.z = 6;
cameraGroup.add(camera);

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
  canvas: canvas,
  alpha: true,
});

renderer.setSize(sizes.width, sizes.height);
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));

/**
 *Scroll
 */
let scrollY = window.scollY;
let currentSection = 0;

window.addEventListener("scroll", () => {
  scrollY = window.scrollY;

  const newSection = Math.round(scrollY / sizes.height);

  /*   if (newSection != currentSection) {
    currentSection = newSection;
    gsap.to(sectionMeshes[currentSection].position, {
      duration: 10.5,
      ease: "power2.inOut",

      x: "+=1",
      y: "+=1",
      z: "+=1",
    });
  } */

  if (newSection != currentSection) {
    currentSection = newSection;

    gsap.fromTo(
      sectionMeshes[currentSection].scale,
      {
        x: 1,
        y: 1,
        z: 1,
      },
      {
        x: 3,
        y: 3,
        z: 3,
        duration: 20,
      }
    );
  }
});

/**
 * Curser
 */

const curser = {};
curser.x = 0;
curser.y = 0;

window.addEventListener("mousemove", (event) => {
  curser.x = event.clientX / sizes.width - 0.5;
  curser.y = event.clientY / sizes.height - 0.5;
  // console.log(curser);
});

/**
 * Animate
 */
const clock = new THREE.Clock();
let previousTime = 0;

const tick = () => {
  const elapsedTime = clock.getElapsedTime();
  const deltaTime = elapsedTime - previousTime;
  previousTime = elapsedTime;

  // Render
  renderer.render(scene, camera);

  //Animate meshes

  for (const mesh of sectionMeshes) {
    mesh.rotation.x += deltaTime * 0.1;
    mesh.rotation.y += deltaTime * 0.12;
  }

  //Animate camera
  camera.position.y = (-scrollY / sizes.height) * objectsDistance;
  // console.log(camera.position.y);

  const parallaxX = curser.x * 0.5;
  const parallaxY = -curser.y * 0.5;
  cameraGroup.position.x +=
    (parallaxX - cameraGroup.position.x) * 5 * deltaTime;
  cameraGroup.position.y +=
    (parallaxY - cameraGroup.position.y) * 5 * deltaTime;

  // Call tick again on the next frame
  window.requestAnimationFrame(tick);
};

tick();
